import React, { Component } from 'react';
import logo from './logo.svg';
import Navbar from "./Components/Navbar";
import Section from "./Components/Section";
import dummyText from "./DummyText";
import aboutText from "./About";
import { NavLink, Switch, Route } from 'react-router-dom';
import { Link, animateScroll as scroll } from "react-scroll";
import './App.css';
const Main = () => (
    <div>
        <div className='home' >
            <div className="parallax">
                <div className="caption">
                    <div className="parallax2"></div>
             </div>
            </div>
        </div>

    </div>
);
const About = () => (
    <div className="about">
        <p> <b> 5th Year Computer Science </b> student at the University of Sheffield, active speaker and maker of random software projects.  </p>
        </div>
)

const Portfolio = () => (
    <div className="portfolio">
        <p> Still being filled out! </p>
    </div>
    )
const Contact = () => (
    <div className="contact">
        <p> The following are my contact details, for any business/professional enquiries, please email <i> dbemben1@sheffield.ac.uk </i> <br/>Talk to me about anything involving programming (Python, Javascript, C), public speaking, or how to set up projects at university!</p>
        <div id='contact-bar'>
            <a href="www"><img class="img-fluid contact-img" src="linkedinicon.png" /></a>
            <a href="www"><img class="img-fluid contact-img" src="githubicon.png" /> </a>
            <a href="mailto:dbemben1@sheffield.ac.uk"><img class="img-fluid contact-img" src="emailicon.png" /> </a>
            <a href="www"><img class="img-fluid contact-img" src="instagramicon.png" /> </a>
        </div>
     </div>
    )
class App extends Component { 
    render() {
        return (
            <div className="App">
                <Navbar />
                <Main />

                <Section
                    title="About"
                    dark={true}
                    id="section1"
                />
                <About/>
                <Section
                    title="Portfolio"
                    dark={false}
                    id="section2"
                />
                <Portfolio/>
                <Section
                    title="Contact"
                    dark={true}
                    id="section3"
                />
                <Contact/>

            </div>

            )
    }
};




export default App;
